import React from 'react';
import InputerForm from '../../Shared/InputerForm';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class RegulatedBuildingAndSafety extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='REGULATED BUILDING AND SAFETY FEATURES' />
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFR_043_Consent_Title' />
          <InputerForm {...this.props} FieldName='SFR_Installed_Items' />
          <InputerForm {...this.props} FieldName='SFR_043_Consent_Note' />
        </div>
        <div className='form_inputs'>
          <InputerForm {...this.props} FieldName='SFR_Consent' />
        </div>
      </>
    );
  }
}
